@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
body {
  font-family: "PT Sans", "Tahoma", sans-serif;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0 auto;
}
img {
  max-width: 100%;
  height: auto;
}
.page__body {
  max-width: 320px;
  min-height: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page__body {
    max-width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__body {
    max-width: 1200px;
  }
}
.header {
  display: flex;
  flex-wrap: wrap;
}
.header__wrapper {
  position: relative;
  width: 100%;
  height: 56px;
  z-index: 3;
}
@media (max-width: 767px) {
  .header__wrapper {
    background-color: #ffffff;
  }
}
.header__logo {
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    margin-top: -54px;
  }
}
@media (max-width: 767px) {
  .header__menu-icon {
    position: absolute;
    display: block;
    top: 17px;
    right: 20px;
  }
}
@media (min-width: 768px) {
  .header__menu-icon {
    display: none;
  }
}
.header__nav {
  order: -1;
}
.header__image {
  position: relative;
  width: 100%;
  text-align: center;
  z-index: 2;
}
@media (min-width: 320px) {
  .header__image {
    background-image: url("../img/bg-back-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-position: -223px 0;
  }
  .header__image::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 57px;
    bottom: -31px;
    left: 0;
    background-image: url("../img/bg-intro-triangle-mobile.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
  }
}
@media (min-width: 320px) and (min-resolution: 2dppx) {
  .header__image {
    background-image: url("../img/bg-back-mobile@2x.jpg");
    background-size: 766px auto;
  }
}
@media (min-width: 768px) {
  .header__image {
    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: 0 -30px;
    margin-top: -56px;
  }
  .header__image::after {
    bottom: 2px;
    background-image: url("../img/bg-intro-triangle-tablet.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .header__image {
    background-image: url("../img/bg-back-tablet@2x.jpg");
    background-size: 767px auto;
  }
}
@media (min-width: 1200px) {
  .header__image {
    background-image: url("../img/bg-back-desktop@1x.jpg");
  }
  .header__image::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 57px;
    left: 0;
    background-image: url("../img/bg-intro-triangle-desktop.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .header__image {
    background-image: url("../img/bg-back-desktop@2x.jpg");
    background-size: 1200px auto;
  }
}
