.feature__title {
  align-self: center;
  display: block;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  color: #000000;
  margin: 0;
  padding: 0;
  padding-top: 57px;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
