/* Phone icon */
.contact__item--tel::before {
  position: absolute;
  content: "";
  width: 58px;
  height: 55px;
  top: 45px;
  right: 0;
  background-color: #E6E6E6;
  background-repeat: no-repeat;
  background-position: 0 0;
}
@media (min-width: 768px) {
  .contact__item--tel::before {
    width: 58px;
    height: 57px;
    top: 43px;
  }
}
@media (min-width: 1200px) {
  .contact__item--tel::before {
    top: 0;
  }
}
.contact__item--tel::after {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  top: 63px;
  right: 19px;
  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
@media (min-width: 1200px) {
  .contact__item--tel::after {
    top: 20px;
    right: 20px;
  }
}
