/* Form attractions */
@media (min-width: 768px) {
  .attractions {
    width: 100%;
    margin-top: 16px;
  }
}
@media (min-width: 768px) {
  .attractions legend {
    width: 308px;
  }
}
@media (min-width: 1200px) {
  .attractions legend {
    padding-right: 24px;
  }
}
.attractions__list {
  display: grid;
  grid-template-columns: 216px;
  gap: 54px;
  margin: 0;
  padding: 0;
  margin-top: 36px;
  margin-bottom: 39px;
}
@media (min-width: 768px) {
  .attractions__list {
    grid-template-columns: 216px 216px;
    justify-content: center;
    gap: 62px;
    margin-left: 12px;
    margin-top: 59px;
    margin-bottom: 54px;
  }
}
@media (min-width: 1200px) {
  .attractions__list {
    grid-template-columns: 216px 216px 216px 216px;
    gap: 36px;
  }
}
.attractions__item {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  margin: 0;
  padding-left: 80px;
}
@media (min-width: 768px) {
  .attractions__item {
    padding-left: 91px;
  }
}
@media (min-width: 1200px) {
  .attractions__item {
    padding-left: 71px;
  }
}
@media (min-width: 768px) {
  .attractions__item:nth-of-type(2n) {
    padding-left: 75px;
  }
}
@media (min-width: 1200px) {
  .attractions__item:nth-of-type(2) {
    padding-left: 77px;
  }
}
@media (min-width: 1200px) {
  .attractions__item:nth-of-type(3) {
    padding-left: 83px;
  }
}
@media (min-width: 1200px) {
  .attractions__item:nth-of-type(4) {
    padding-left: 70px;
  }
}
.attractions__input + label::before {
  content: "";
  position: absolute;
  left: -80px;
  top: -17px;
  width: 55px;
  height: 55px;
  background-color: #F2F2F2;
  border-radius: 5%;
}
.attractions__input + label::before:hover {
  background-color: #E5E5E5;
}
.attractions__input + label::before:active {
  background-color: #D5D5D5;
}
.attractions__input:checked + label::after {
  content: "";
  position: absolute;
  left: -64px;
  top: 4px;
  width: 24px;
  height: 17px;
  background-image: url("../img/tick.png");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.attractions__input:checked + label::after:active {
  opacity: 0.3;
}
.attractions__item label {
  position: relative;
  display: block;
  font-size: 18px;
  font-weight: bold;
  text-transform: none;
  margin: 0;
  padding: 0;
}
/* Form survey */
@media (min-width: 768px) {
  .survey {
    position: relative;
    order: -1;
  }
  .survey::after {
    position: absolute;
    content: "";
    width: 1px;
    height: 292px;
    top: 0;
    right: -52px;
    background-color: #DBDBDB;
    background-repeat: no-repeat;
    background-position: 0 0;
  }
}
@media (min-width: 1200px) {
  .survey {
    width: 352px;
  }
  .survey::after {
    right: -129px;
  }
}
