/* Video-menu timeline */
.timeline__range {
  position: relative;
  width: 100%;
  height: 45px;
  background-color: rgba(0, 0, 0, 0.6);
  margin-top: -45px;
}
@media (min-width: 768px) {
  .timeline__range {
    background-color: inherit;
    width: 389px;
    margin: 0;
    opacity: 1;
  }
}
@media (min-width: 1200px) {
  .timeline__range {
    width: 540px;
  }
}
.timeline__range .timeline__scale {
  position: absolute;
  height: 3px;
  width: 235px;
  top: 22px;
  left: 22px;
  background-color: #ffffff;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .timeline__range .timeline__scale {
    left: 0;
    width: 389px;
  }
}
@media (min-width: 1200px) {
  .timeline__range .timeline__scale {
    top: 20px;
    width: 540px;
  }
}
.timeline__range .timeline__toggle {
  position: absolute;
  top: 18px;
  left: 57px;
  width: 10px;
  height: 10px;
  padding: 0;
  background-color: #ffffff;
  opacity: 1;
  border-radius: 50%;
  cursor: pointer;
}
@media (min-width: 768px) {
  .timeline__range .timeline__toggle {
    left: 111px;
  }
}
@media (min-width: 1200px) {
  .timeline__range .timeline__toggle {
    top: 15px;
  }
}
